@font-face {
  font-family: "Roboto";
  font-weight: 300;
  font-style: normal;
  src: url("../fonts/roboto-lite-latin.woff") format("woff"), url("../fonts/roboto-lite-latin.woff2") format("woff2"); }

@font-face {
  font-family: "Info";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/info-normal.woff") format("woff"), url("../fonts/info-normal.woff2") format("woff2"); }

@font-face {
  font-family: "Info";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/info-bold.woff") format("woff"), url("../fonts/info-bold.woff2") format("woff2"); }

body {
  min-width: 1020px;
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-weight: 300;
  font-family: "Roboto", "Arial", sans-serif;
  line-height: 22px;
  color: #000000;
  background-color: #ffffff; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

